//container
.container {
    max-width: 1200px;
    width: 98%;
    margin: 0 auto;
    // background: rgb(83, 82, 82);
}

//layout
#main {
    background-color: var(--black);
}

//cont__title
.cont__title {
    padding: 15vmax 1% 10vmax;
    font-size: 5vmax;
    font-family: var(--main_font);
    text-transform: uppercase;
    line-height: 0.85;
    color: var(--white);

    em {
        display: block;
    }
}
@media (max-width: 600px) {
    .cont__title {
        text-align: center;
        padding-bottom: 10vw;
    }
}

//타이틀 애니메이션
.cont__title {

    strong {
        display: block;
        opacity: 0;
        transform: translateX(0px), translateY(50px);
    }

    em {
        display: block;
        opacity: 0;
        transform: translateX(0px), translateY(50px);
    }
    
}

//cont__contact
.cont__contact {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--white);

    
        p {
            font-size: 1.4rem;
            font-family: var(--sub_font);
            margin-bottom: 4vw;
        }
        h3 {
            font-family: var(--main_font);
            font-size: 9vw;
            text-align: center;
            line-height: 1;
            // text-transform: uppercase;
            margin-bottom: 4vw;
    
            em {
                display: block;
            }
        }
        a {
            font-size: 1rem;
            font-family: var(--sub_font);
            text-transform: uppercase;
            border: 1px solid var(--white);
            border-radius: 50px;
            padding: 1vw 3vw;
    }
}

//라이트 버전
.light.cont__title {
    background-color: var(--light_bg);
    border-color:var(--light_border);

    div {
        color: var(--black);

        &::before {
            background: var(--black);
        }
    }
}
.light.cont__contact {
    background-color: var(--light_bg);
    border-color:var(--light_border);
    color: var(--black);

    // div {
    //     color: var(--black);

    //     &::before {
    //         background: var(--black);
    //     }
    // }
}




