//port__cont
.port__cont {
   padding-bottom: 20vh;
}
.port__inner {
    opacity: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 100px;
}
.port__item {
    flex: 1 1 30%;
    margin: 1%;

    .img {}
    .text {
        padding: 1.4em;
        background-color: #151517;

        h3 {
            color: var(--white);
            font-family: var(--main_font);
            font-size: 2.6rem;
            line-height: 1;
            padding-top: 0.2em;
            text-transform: uppercase;
        }
        p {
            color: var(--white);
            font-family: var(--sub_font);
        }
        
    }
}

//애니메이션 
.port__inner {
    opacity: 0;
    transform: translateX(0px), translateY(50px);
}